.nav-cont {
  background-color: rgb(255, 34, 79);
  display: flex;
  gap: .5rem;
  color: white;
  padding: 1.5rem;
  font-weight: 600;
  font-size: 1.1rem;
  justify-content: center;
  border-radius: 15px;
}

.images {
  width: 200px;
  border-radius: 15px;
}

.card-cont {
  display: flex;
  gap: 2rem;
  padding: 1rem 2rem;
  margin-top: 1rem;
}

.card-cont:first-child {
  margin-top: 3rem;
}

.text {
  padding: 1.5rem 0;
}

.first-row {
  height: 2rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: .1rem;
}

.first-row a {
  margin-left: 1rem;
}

.text h1 {
  padding: .5rem 0 1.5rem 0;
}

.text p {
  width: 90%;
  line-height: 1.5rem;
  opacity: .7;
  margin-top: 0.5rem;
}